export function loadCustomStyle(url: string): Promise<HTMLLinkElement> {
  const head = document.getElementsByTagName('head')[0];
  const link = document.createElement('link');
  link.rel = 'stylesheet';
  link.href = url;
  const isReady: Promise<HTMLLinkElement> = new Promise(resolve => {
    link.onload = () => resolve(link)
  })
  head.appendChild(link);
  return isReady
}
